<template>
  <div>
    <h2>登入會員</h2>
    <form @submit.prevent="handleSubmit">
      <div>
        <label>
          使用者名稱：
          <input type="text" v-model="username">
        </label>
      </div>
      <div>
        <label>
          密碼：
          <input type="password" v-model="password">
        </label>
      </div>
      <button type="submit">登入</button>
    </form>
  </div>
</template>

<script>
export default {
  name: 'LoginMember',
  data() {
    return {
      username: '',
      password: ''
    };
  },
  methods: {
    handleSubmit() {
      alert('登入成功！');
    }
  }
}
</script>
