<template>
  <div>
    <h2>註冊</h2>
    <form @submit.prevent="handleSubmit">
      <div>
        <label>
          使用者名稱：
          <input type="text" v-model="username">
        </label>
      </div>
      <div>
        <label>
          密碼：
          <input type="password" v-model="password">
        </label>
      </div>
      <div>
        <label>
          電子郵件：
          <input type="email" v-model="email">
        </label>
      </div>
      <button type="submit">註冊</button>
    </form>
  </div>
</template>

<script>
export default {
  name: 'RegisterMember',
  data() {
    return {
      username: '',
      password: '',
      email: ''
    };
  },
  methods: {
    handleSubmit() {
      alert('註冊成功！');
    }
  }
}
</script>
